.tooltip-icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url(../images/tooltipIcon.svg) 0 0 no-repeat;
}

.vote-tooltip {
  .ant-tooltip-arrow {
    width: 40px;
    top: -4px;

    .ant-tooltip-arrow-content {
      width: 15px;
      height: 15px;
    }
  }
}

.vote-cast-tip.ant-tooltip {
  .ant-tooltip-arrow {
    transform: rotateX(180deg);
    top: unset;
    bottom: 2px;
    left: unset;
    right: 12px;
  }
}

.vote-deposit {
  .ant-pagination {
    text-align: right;
  }

  .vote-authorize-logo {
    width: 50px;
    height: 50px;
    background: url(../images/voteAuthorize.svg) center 0 no-repeat;
    margin: 0 auto;
  }

  .vote-text {
    width: 180px;
    line-height: 24px;
    margin-top: 14px;
  }

  .ant-modal {
    top: calc(50% - 100px);
  }

  a.toSwap {
    color: #3d56d6;
    margin-left: 5px;
  }

  &.border-bottom-header {
    .ant-modal-title {
      &::after {
        display: none;
      }
    }
  }
}

.border-bottom-header {
  .ant-modal-header {
    margin-bottom: 12px;
  }

  .ant-modal-title {
    position: relative;

    &::after {
      content: '';
      display: block;
      width: 100%;
      height: 1px;
      background-color: #f2f2f2;
      position: absolute;
      left: 0;
      top: 36px;
    }
  }
}

.votefor-modal {
  .horizontal-line {
    margin: 20px auto;
  }

  &.ant-modal {
    top: calc(50% - 200px);

    a.vote-deposit-link {
      color: #3d56d6;
      margin-left: 5px;
      font-family: 'PingFang SC' !important;
    }

    button {
      &:disabled {
        background: #f0f1f6;
        color: #84869e;
        cursor: not-allowed;
      }
    }
  }
}

.wait-detail {
  height: 58px;
  padding: 20px;
  background: #f5f6f9;
  border-radius: 4px;
}

.please-login-first {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: calc(100vh - 40px);
}

.ant-table-empty .ant-table-tbody > tr.ant-table-placeholder {
  td {
    height: auto;
    border: none;
  }
}

.ant-modal.vote-detail-modal {
  .ant-modal-close-x {
    top: 0;
    right: 0;
    width: 56px !important;
    height: 56px !important;
    background: url(../images/mobile/close-icon.svg) center no-repeat;
    background-size: 12px 12px;
  }
  .vote-detail-container {
    .box {
      background: rgba($color: #3d56d6, $alpha: 0.06);
      border-radius: 6px;
      padding: 20px;
      &:nth-child(1) {
        .vote-detail-item {
          &:nth-child(1) {
            margin-top: 0px;
          }
        }
      }
      &.full {
        .vote-detail-item {
          .point {
            width: 100%;
            &.zh {
              width: 100%;
            }
          }
        }
      }
      .vote-detail-item {
        display: flex;
        justify-content: space-between;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #0f134f;
        margin-top: 24px;
        align-items: center;
        width: 100%;
        // flex-wrap: wrap;
        &:nth-child(1) {
          margin-top: 4px;
        }
        .point {
          display: inline-flex;
          flex-direction: column;
          justify-content: center;
          background: url(../images/vote_li_point.svg) left 8px no-repeat;
          background-size: 4px;
          padding-left: 10px;
          width: calc(100% - 93px);

          &.zh {
            width: calc(100% - 65px);
          }
        }
        .link {
          background: #3d56d6;
          border-radius: 4px;
          color: #fff;
          padding: 4px 10px;
          font-weight: 400;
          font-size: 12px;
          margin-left: 20px;
          cursor: pointer;
          width: auto;
        }
        .title {
          width: 100%;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
        }
        .amount {
          margin-left: 0px;
          margin-top: 4px;
          span {
            font-weight: 600;
          }
        }
      }
    }
    .tip {
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      color: #0f134f;
      margin: 4px 0 10px;
      margin-left: 20px;
    }
  }
  .no-data {
    text-align: center;
    padding: 74px 0;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: #545669;
    border-radius: 6px;
    background: rgba($color: #3d56d6, $alpha: 0.06) url(../images/vote_no_data.svg) center center no-repeat;
    background-size: 60px;
  }
}
.ant-modal.vote-old-withdraw-modal {
  .ant-modal-close-x {
    right: 0;
    top: 0;
    width: 56px !important;
    height: 56px !important;
    background: url(../images/mobile/close-icon.svg) center no-repeat;
    background-size: 12px 12px;
  }

  .ant-modal-body {
    padding: 0;
  }
  .vote-old-withdraw-content {
    padding: 24px;
    border-top: 1px solid rgba($color: #0f134f, $alpha: 0.05);
    .link {
      width: auto;
    }
    .title {
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: #0f134f;
      text-align: center;
    }
    .content {
      margin-top: 10px;
      background: rgba($color: #3d56d6, $alpha: 0.06);
      border-radius: 6px;
      padding: 16px 20px;
      .point {
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        color: #545669;
        background: url(../images/vote_li_point.svg) left 6px no-repeat;
        background-size: 4px;
        padding-left: 10px;

        span {
          font-weight: 400;
          font-size: 12px;
          line-height: 17px;

          &.bold {
            font-weight: 600;
            color: #0f134f;
          }
        }

        &.flex-sp {
          margin-top: 10px;
          display: flex;
          justify-content: space-between;
        }
      }

      a {
        color: #0f134f;
        font-weight: 400;
        font-size: 12px;
        line-height: 17px;
        background: url(../images/vote_right_arrow.svg) right no-repeat;
        background-size: 6px;
        padding-right: 10px;
      }
    }
    .withdraw-btn {
      background: #3d56d6;
      border-radius: 8px;
      margin-top: 20px;
      padding: 10px 0;
      width: 100%;
      text-align: center;
      color: #fff;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      cursor: pointer;
    }
  }
}

@media screen and (max-width: 1000px) {
  .vote-container {
    .vote-content {
      display: block;
    }

    .vote-left,
    .vote-right {
      width: 100%;
    }

    .vote-left {
      margin-bottom: 20px;
    }
  }

  .vote-container .vote-left .vl-list > a .card-list {
    height: 120px;

    > div:nth-child(2) {
      display: block;
    }

    .cl-footer-left {
      margin-bottom: 10px;
    }
  }

  .vote-detail-container .progress-info-container {
    display: block;

    .progress-info {
      width: 100%;
    }
  }

  .vote-detail-info-container {
    display: block;

    .vote-detail-info,
    .vote-detail-timeline {
      width: 100%;
    }
  }

  .status {
    height: auto;
    line-height: unset;
    padding: 2px 0;
  }

  .please-login-first {
    height: calc(100vh - 83px);
  }
}

